@tailwind base;
@tailwind components;
@tailwind utilities;

/* ðŸ‘‡ Add your global defaults below Tailwind layers */
body {
  @apply text-base text-gray-900 font-sans;
}
/* Hide scrollbar but keep scroll functionality */
.scrollbar-none::-webkit-scrollbar {
  display: none;
}
.scrollbar-none {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none;    /* Firefox */
}
